@import "https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap";
:root {
  --bg-color: #f6f8fa;
  --text-color: #1d1f27;
  --main-color: #3a3dbe;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--bg-color);
  color: var(--text-color);
  min-height: 100vh;
  font-family: Poppins, sans-serif;
}

.title-container {
  justify-content: center;
  align-items: center;
  width: 100%;
  padding-top: 20px;
  display: flex;
}

h3, #header {
  margin-bottom: .5rem;
}

main {
  background-color: #fff;
  flex-direction: column;
  gap: 16px;
  max-width: 80%;
  margin: 1rem auto;
  padding: 1rem;
  display: flex;
  box-shadow: 0 3px 5px #0000001a;
}

.signin-container {
  flex-direction: column;
  align-items: center;
  gap: 16px;
  width: 250px;
  margin: auto;
  padding: 20px;
  display: flex;
}

#header {
  background-color: var(--main-color);
  color: #fff;
  text-align: center;
  border-radius: 5px;
  flex-wrap: wrap;
  padding: 1rem;
  display: flex;
}

#header div {
  padding: 5px;
}

#header div:first-child {
  flex-basis: 100%;
  font-size: 2.25rem;
  font-weight: 600;
}

#header div:nth-child(n+2) {
  flex-basis: 50%;
  font-size: 1.25rem;
  font-weight: 500;
}

#header h5 {
  text-transform: uppercase;
  font-size: .75rem;
  font-weight: 600;
}

form {
  grid-template-columns: 1fr 1fr;
  gap: 8px;
  display: grid;
}

form input:not(#type), form button, #googleSignInBtn, #googleSignOutBtn, #aiInput, #aiButton {
  border: 1px solid #ddd;
  border-radius: 5px;
  width: 100%;
  height: 42px;
  margin-bottom: 5px;
  padding: 10px;
  font-family: Poppins, sans-serif;
  font-size: 1rem;
}

form button, #googleSignInBtn, #googleSignOutBtn, #aiButton {
  background-color: var(--main-color);
  color: #fff;
  text-transform: uppercase;
  grid-column: span 2;
  font-size: .75rem;
  font-weight: 600;
  box-shadow: 0 3px 5px #0000001a;
}

form button:hover, #googleSignInBtn:hover, #googleSignOutBtn:hover, #aiButton:hover {
  cursor: pointer;
  background: #0056b3;
}

form label {
  text-transform: uppercase;
  font-size: .75rem;
  font-weight: 600;
  display: block;
}

form div:nth-child(-n+2) {
  grid-column: span 2;
}

form div:nth-child(n+3) {
  flex-basis: calc(50% - 5px);
}

input#type {
  appearance: none;
  position: absolute;
}

.option {
  cursor: pointer;
  color: #fff;
  background-color: #9acd32;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 10px;
  transition: background-color .25s;
  display: flex;
  position: relative;
}

.option:before {
  content: attr(data-option);
}

.option:after {
  content: "";
  border-radius: inherit;
  background-color: #fff;
  width: calc(50% - 10px);
  height: calc(100% - 10px);
  transition: left .2s;
  position: absolute;
  top: 5px;
  left: 0;
  transform: translateX(5px);
  box-shadow: 0 2px 2px #0000001a;
}

.option:before, .option span {
  text-align: center;
  width: 50%;
  cursor: inherit;
  z-index: 2;
}

input#type:checked ~ .option:after {
  left: 50%;
}

input#type:checked ~ .option {
  background-color: #cd5c5c;
}

input#type:checked ~ .option span, input#type:not(:checked) ~ .option:before {
  color: #1d1f27;
}

input[type="date"] {
  appearance: none;
  color: #1d1f27;
  text-align: start;
  background-color: #fff;
}

ul {
  height: 200px;
  margin-bottom: .5rem;
  list-style-type: none;
  overflow-y: auto;
}

ul:empty {
  border: 2px dashed #ddd;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  display: flex;
}

ul:empty:before {
  content: attr(data-placeholder);
}

ul li {
  justify-content: space-between;
  align-items: flex-start;
  margin-block: 5px;
  padding-inline: 10px;
  display: flex;
  position: relative;
}

ul li:hover {
  cursor: pointer;
  background: #f6f8fa;
}

.name {
  flex: 1;
}

.name h4 {
  text-transform: capitalize;
  font-size: 1rem;
  font-weight: 600;
}

.name p {
  color: #555;
  font-size: .8rem;
}

.amount {
  font-weight: 600;
}

.amount.income {
  color: #9acd32;
}

.amount.expense {
  color: #cd5c5c;
}

#monthly-view {
  width: 100%;
  margin-top: 24px;
}

.transaction-item {
  justify-content: space-between;
  display: flex;
}

#chatbot-container {
  background: #f5f5f5;
  border: 1px solid #ddd;
  border-radius: 8px;
  flex-direction: column;
  gap: 8px;
  width: 100%;
  max-width: 400px;
  height: 700px;
  margin: 5px auto;
  padding: 5px 15px;
  display: flex;
}

#chatHistory {
  background: #fff;
  border: 1px solid #ddd;
  height: 100%;
  margin-bottom: 10px;
  padding: 10px;
  overflow-y: auto;
}

.history {
  text-align: left;
  background: #e9e9e9;
  border-radius: 5px;
  margin: 5px 0;
  padding: 5px;
}

.visually-hidden {
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

@media (width >= 1200px) {
  main {
    flex-direction: row;
    justify-content: space-around;
  }

  #monthly-view {
    width: 50%;
    margin-top: 0;
  }
}
